import React from 'react';
import NumberFormat from 'react-number-format';

const CustomInput = ({ value, onChange }) => <input value={value} onChange={onChange} />

const CurrencyInput = ({ value, onChange, decimalScale = 2, prefix = 'R$ ' }) => {
  let values = {};

  // This implementation solves a behavior in the react-number-format that was calling onChangeValue when the component props was beeing updated.
  // Issue that describe the behavior: https://github.com/s-yadav/react-number-format/issues/264

  const onValueChange = formattedValues => {
    values = formattedValues;
  };

  const handleChange = () => Object.keys(values).length > 0 && onChange(values);

  return (
    <NumberFormat
      allowEmptyFormatting
      allowNegative={false}
      customInput={CustomInput}
      decimalScale={decimalScale}
      decimalSeparator=","
      fixedDecimalScale
      margin="0"
      onValueChange={onValueChange}
      onChange={handleChange}
      prefix={prefix}
      size="large"
      thousandSeparator="."
      value={value}
    />
  );
};

export default CurrencyInput;
