import React from 'react';
import CurrencyInputV1 from './CurrencyInputV1';

export default () => {
  const [currencyInputV1, setCurrencyInputV1] = React.useState({
    formattedValue: '',
    floatValue: 0,
    decimalScale: 2,
    prefix: 'R$ '
  })

  return (
    <div style={{ paddingRight: 10, paddingLeft: 10 }}>
      <h2>Old CurrencyInput</h2>
      <div>
        Decimals: <input
          type='number'
          value={currencyInputV1.decimalScale}
          min="0"
          onChange={({ target: { value } }) => {
            setCurrencyInputV1({
              ...currencyInputV1,
              decimalScale: value
            })
          }}
        />
      </div>

      <div>
        Prefix : <input
          value={currencyInputV1.prefix}
          onChange={({ target: { value } }) => {
            setCurrencyInputV1({
              ...currencyInputV1,
              prefix: value
            })
          }}
        />
      </div>

      <div style={{ marginTop: 20 }}>
        Input: <CurrencyInputV1
          value={currencyInputV1.floatValue}
          decimalScale={currencyInputV1.decimalScale}
          prefix={currencyInputV1.prefix}
          onChange={(e, maskedValue, floatValue) =>
            setCurrencyInputV1({
              ...currencyInputV1,
              formattedValue: maskedValue,
              floatValue,
            })
          }
        />
      </div>

      <div>
        <h4 style={{ marginBottom: 0 }}>
          FormattedValue: "{currencyInputV1.formattedValue}"
        </h4>
        <h4 style={{ marginTop: 0, marginBottom: 1 }}>
          FloatValue: {currencyInputV1.floatValue}
        </h4>
      </div>
    </div>
  )
}
