import React from 'react';
import ReactNumberFormat from './ReactNumberFormat';

export default () => {
  const [reactNumberFormat, setReactNumberFormat] = React.useState({
    formattedValue: '',
    floatValue: 0,
    decimalScale: 2,
    prefix: 'R$ '
  })

  return (
    <div style={{paddingRight: 10, paddingLeft: 10}}>
      <h2>ReactNumberFormat</h2>
      <div>
        Decimals: <input
          type='number'
          value={reactNumberFormat.decimalScale}
          min="0"
          onChange={({ target: { value } }) => {
            setReactNumberFormat({
              ...reactNumberFormat,
              decimalScale: value
            })
          }}
        />
      </div>

      <div>
        Prefix : <input
          value={reactNumberFormat.prefix}
          onChange={({ target: { value } }) => {
            setReactNumberFormat({
              ...reactNumberFormat,
              prefix: value
            })
          }}
        />
      </div>

      <div style={{ marginTop: 20 }}>
        Input: <ReactNumberFormat
          value={reactNumberFormat.floatValue}
          decimalScale={reactNumberFormat.decimalScale}
          prefix={reactNumberFormat.prefix}
          onChange={
            ({
              formattedValue,
              floatValue,
            }) => setReactNumberFormat({
              ...reactNumberFormat,
              formattedValue,
              floatValue,
            })
          }
        />
      </div>

      <div>
        <h4 style={{ marginBottom: 0 }}>
          FormattedValue: "{reactNumberFormat.formattedValue}"
        </h4>
        <h4 style={{ marginTop: 0, marginBottom: 1 }}>
          FloatValue: {reactNumberFormat.floatValue}
        </h4>
      </div>
    </div>
  )
}
