import React from 'react';
import CurrencyInput from 'react-currency-input';

const CurrencyInputV1 = ({ value, onChange, prefix, decimalScale }) =>
  <CurrencyInput
    allowEmpty
    prefix={prefix}
    decimalSeparator=","
    thousandSeparator="."
    onChangeEvent={onChange}
    value={value}
    precision={decimalScale}
  />

export default CurrencyInputV1;
